<template>
  <div class="list-container">
    <div v-for="(comp, index) in displayList" :key="index">
      <el-row class="list-item">
        <el-col :span="3">
          <div class="img-container">
            <img src="../assets/logo.png" />
          </div>
        </el-col>
        <el-col :span="6"> {{ comp.name }} </el-col>
        <el-col :span="6"> {{ comp.address }} </el-col>
        <el-col :span="3" class="grid-content"></el-col>
        <el-col :span="3" class="grid-content"></el-col>
        <el-col :span="3">
          <el-button @click="jumpTo(comp.id)">赛事详情</el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { getList } from "../api/competitions";

export default {
  name: "CompetitionList",
  props: {},
  data() {
    return {
      displayList: [],
    };
  },
  created() {
    getList().then((data) => {
      this.displayList = data;
    });
  },
  methods: {
    jumpTo(id) {
      this.$router.push({
        name: "competition",
        query: { id },
      });
    },
  },
};
</script>


<style scoped>
.list-container > div {
  margin-bottom: 10px;
}

.list-container :last-child {
  margin-bottom: 0;
}

.list-item {
  padding: 10px;
  line-height: 90px;
  background-color: #ffffff;
  border-radius: 6px;
}

.img-container {
  height: 90px;
  width: 90px;
}

.img-container img {
  height: 100%;
}

.grid-content {
  min-height: 36px;
}
</style>
